import React, { useState, useEffect } from "react";
import { Form, Input, message } from "antd";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import Spinner from "../components/Spinner";

const Login = () => {
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const submitHandler = async (values) => {
    try {
      setLoading(true);
      const { data } = await axios.post("/api/v1/users/login", values);
      setLoading(false);
      message.success("Loggedin Successfully");
      localStorage.setItem(
        "user",
        JSON.stringify({ ...data.user, password: "" })
      );
      navigate("/");
    } catch (error) {
      setLoading(false);
      message.error("Something went wrong");
    }
  };

  // prevent for login user
  useEffect(() => {
    if (localStorage.getItem("user")) {
      navigate("/");
    }
  }, [navigate]);

  return (
    <>
      <div
        className="register-page"
        style={{
          backgroundImage:
            "linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.9)), url('/path/to/background/image.jpg')",
          backgroundSize: "cover",
          backgroundPosition: "center",
        }}
      >
        {loading && <Spinner />}
        <Form
          layout="vertical"
          onFinish={submitHandler}
          style={{
            maxWidth: "400px",
            margin: "0 auto",
            padding: "50px",
            backgroundColor: "#fff",
            borderRadius: "5px",
            boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
          }}
        >
          <h1
            style={{
              textAlign: "center",
              marginBottom: "50px",
              color: "#555",
              fontWeight: "bold",
            }}
          >
            Login
          </h1>
          <Form.Item
            label="Email"
            name="email"
            rules={[
              {
                required: true,
                message: "Please input your email address",
              },
              {
                type: "email",
                message: "Please enter a valid email address",
              },
            ]}
          >
            <Input
              type="email"
              style={{
                backgroundColor: "#f5f5f5",
                border: "none",
                borderRadius: "3px",
                padding: "10px",
              }}
            />
          </Form.Item>
          <Form.Item
            label="Password"
            name="password"
            rules={[
              {
                required: true,
                message: "Please enter your password",
              },
            ]}
          >
            <Input.Password
              style={{
                backgroundColor: "#f5f5f5",
                border: "none",
                borderRadius: "3px",
                padding: "10px",
              }}
            />
          </Form.Item>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              marginTop: "20px",
            }}
          >
            <Link
              to="/register"
              style={{
                color: "#555",
                textDecoration: "none",
                fontWeight: "bold",
                fontSize: "14px",
              }}
            >
              Not a user? Register here
            </Link>
            <button
              className="btn btn-primary"
              style={{
                backgroundColor: "#f44336",
                color: "#fff",
                border: "none",
                borderRadius: "3px",
                padding: "10px 20px",
                fontSize: "14px",
                fontWeight: "bold",
                cursor: "pointer",
                transition: "all 0.3s",
              }}
            >
              Login
            </button>
          </div>
        </Form>
      </div>
    </>
  );
};

export default Login;
