import React from 'react';
import { FaGithub, FaLinkedin, FaEnvelope } from 'react-icons/fa';

const Footer = () => {
  return (
    <footer className="bg-dark text-light py-3">
      <div className="container d-flex justify-content-between align-items-center">
        <div className="social-icons">
          <a href="https://github.com/rahulgithub-web" target="_blank" rel="noopener noreferrer" className="text-light me-3">
            <FaGithub />
          </a>
          <a href="https://www.linkedin.com/in/rk-barnwal23/" target="_blank" rel="noopener noreferrer" className="text-light me-3">
            <FaLinkedin />
          </a>
          <a href="rahulbarnwalonlyu2@gmail.com" className="text-light me-3">
            <FaEnvelope />
          </a>
        </div>
        <p className="mb-0 text-center">All rights reserved &copy; {new Date().getFullYear()} Rahul Kumar</p>
      </div>
    </footer>
  );
};

export default Footer;
